.navBar {
    display: flex;
    justify-content: space-between;
    padding: 0 48px 0 48px;
    align-items: center;
    background-color: black;
    color: white;
    height: 100px;
    font-size: 24px;
}

.links {
    display: flex;
    gap: 72px;
    justify-content: space-around;
    align-items: center;
    color: white;
}

.headerLinks {
    text-decoration: none;
    color: white;
}

li {
    list-style: none;
}
